  }
  header .hamburger::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    aspect-ratio: 1/1;
    border: 1px solid #2D4A37;
    border-radius: 7px;
